


















export default

	props:
		lmap:
			required: true
		boundingBox:
			required: true
		establishments:
			required: true
			type: Object
		tiles:
			required: true
			type: Object

	data: () ->
		display:
			boundingBox: true
			establishments: true
			tiles: true

	methods:
		handleBoxDisplay: (display) ->
			if display
				this.boundingBox.addTo(this.lmap)
			else
				this.boundingBox.remove()

		handleEstablishmentsDisplay: (display) ->
			self = this
			if display
				for _, establishment of this.establishments
					do (establishment) ->
						establishment.mapObject.addTo(self.lmap)
			else
				for _, establishment of this.establishments
					do (establishment) ->
						establishment.mapObject.remove()

		handleTilesDisplay: (display) ->
			self = this
			if display
				for tileLine in Object.values(this.tiles)
					do (tileLine) ->
						for tile in Object.values(tileLine)
							do (tile) ->
								tile.mapObject.addTo(self.lmap)
			else
				for tileLine in Object.values(this.tiles)
					do (tileLine) ->
						for tile in Object.values(tileLine)
							do (tile) ->
								tile.mapObject.remove()

		refresh: () ->
			self = this
			for option, _ of this.display
				do (option, _) ->
					self.display[option] = true


