













export default
	
	props:
		label:
			type: String

	data: () ->
		loadedData: {}
		file: null

	methods:
		handleChange: (file) ->
			this.file = file
			this.$emit("select", file)

		load: () ->
			self = this
			reader = new FileReader()
			reader.onload = () ->
				self.loadedData = JSON.parse(reader.result)
				self.$emit("loaded", self.loadedData)

			console.log this.file
			reader.readAsText(this.file)

		getFile: () ->
			this.file



