




















































































import Viewer from './components/viewer/Viewer.vue'
import Lez from './components/lez/Lez.vue'

export default
	name: 'app'
	components:
		lez: Lez
		viewer: Viewer
	
	data: () ->
		selectedTab: "lez"
		exampleLez: ""
		exampleTiles: ""

	methods:
		selectLez: () ->
			this.selectedTab = "lez"

		selectViewer: () ->
			this.selectedTab = "viewer"

		loadExample: (lez, tiles) ->
			this.exampleLez = lez
			this.exampleTiles = tiles

