












export default

	props:
		tile:
			type: Object
			required: true

	data: () ->
		pollutants: ["N2O", "NOx", "NH3", "VOC", "PM", "CH4", "CO", "FC"]

	mounted: () ->
		this.$watch(
			"tile", (newTile, oldTile) ->
				newTile.mapObject.bindPopup(this.$refs.popupBody.innerHTML).openPopup()
			, immediate: true
			)

